/**
 * ARQUITETURA DE PROMPTS - IMPORTANTE
 *
 * 1. DEFAULT_PROMPTS (hardcoded): Contm instrues tcnicas sobre:
 *    - Como a IA deve se comportar (papel/funo)
 *    - Formato de resposta esperado (JSON, texto, etc.)
 *    - Estrutura de dados obrigatria
 *    - Regras de parsing e validao
 *
 * 2. PROMPTS GLOBAIS (banco de dados): Devem conter APENAS:
 *    - Contexto especfico do domnio/negcio
 *    - Informaes contextuais adicionais
 *    - Preferncias especficas do cliente/usurio
 *    - NUNCA instrues tcnicas sobre formato de resposta
 *
 *    EXEMPLOS de prompts globais (APENAS contexto):
 *    - "Este  um sistema de e-commerce para vendas online"
 *    - "Cliente trabalha com gesto de clnicas mdicas"
 *    - "Priorizar segurana e conformidade com LGPD"
 *    - "Sistema deve ser compatvel com dispositivos mveis"
 *
 * 3. CONTEDO DINMICO:
 *    - Contedo dos documentos do projeto
 *    - Informaes adicionais fornecidas pelo usurio
 *
 * Combinao final: Instrues Tcnicas + Contexto Especfico + Contedo do Projeto
 */

