// Cache para prompts globais
let cachedPrompts: any = null;
let cacheTimestamp: number = 0;
const CACHE_DURATION = 5 * 60 * 1000; // 5 minutos

// Funo para obter prompts globais do banco de dados
// NOTA: Os prompts globais devem conter APENAS contexto especfico do domnio/negcio
// Instrues tcnicas sobre formato de resposta ficam nos DEFAULT_PROMPTS
export const getGlobalPrompts = async (): Promise<any> => {
  try {
    // Verificar cache
    const now = Date.now();
    if (cachedPrompts && (now - cacheTimestamp) < CACHE_DURATION) {
      return cachedPrompts;
    }

    // Buscar do banco de dados (apenas prompts ativos)
    const { data, error } = await supabase
      .from('global_prompts')
      .select('*')
      .eq('is_active', true);

    if (error && error.message.includes('relation "global_prompts" does not exist')) {
      // Usar prompts padro se a tabela no existir
      cachedPrompts = DEFAULT_PROMPTS;
      cacheTimestamp = now;
      return cachedPrompts;
    } else if (error) {
      console.error('Erro ao buscar prompts globais:', error);
      return DEFAULT_PROMPTS;
    }

    // Converter array para objeto - apenas contexto especfico, no instrues tcnicas
    const promptsObject: any = { ...DEFAULT_PROMPTS }
    if (data && Array.isArray(data)) {
      data.forEach(prompt => {
        if (prompt.type && prompt.content) {
          // Se o contedo for diferente do padro, assume que  contexto especfico
          if (prompt.content !== DEFAULT_PROMPTS[prompt.type as keyof typeof DEFAULT_PROMPTS]) {
            promptsObject[prompt.type] = prompt.content;
          }
        }
      });
    }

    cachedPrompts = promptsObject;
    cacheTimestamp = now;
    return cachedPrompts;
  } catch (error) {
    console.error('Erro ao obter prompts globais:', error);
    return DEFAULT_PROMPTS;
  }
}

// Funo para invalidar cache de prompts
export const invalidatePromptsCache = () => {
  cachedPrompts = null;
  cacheTimestamp = 0;
}

// Funo para gerar prompt especfico baseado no tipo
// Funo para buscar materiais de apoio de um projeto
const getProjectSupportMaterials = async (projectId: string) => {
  try {
    console.log(' Buscando materiais de apoio do projeto:', projectId);

    // Primeiro tentar buscar materiais especficos do projeto
    let { data, error } = await supportMaterials.list(projectId);

    // Se no encontrar materiais especficos, buscar materiais padro/globais
    if (!data || data.length === 0) {
      console.log(' Nenhum material especfico encontrado, buscando materiais padro...');
      const { data: defaultData, error: defaultError } = await supabase
        .from('support_materials')
        .select('*')
        .eq('is_default', true)
        .order('created_at', { ascending: false });

      if (!defaultError && defaultData && defaultData.length > 0) {
        console.log(' Materiais padro encontrados:', defaultData.length);
        data = defaultData;
      }
    }

    if (error) {
      console.error(' Erro ao buscar materiais de apoio:', error);
      return {};
    }

    // Organizar por tipo
    const materialsByType: any = {};
    (data || []).forEach(material => {
      materialsByType[material.type] = material.content;
      console.log(` Material ${material.type} encontrado:`, material.content.substring(0, 100) + '...');
      console.log(` Detalhes do material ${material.type}:`, {
        id: material.id,
        name: material.name,
        type: material.type,
        is_default: material.is_default,
        content_length: material.content.length
      });
    });

    console.log(' Materiais de apoio encontrados:', Object.keys(materialsByType));
    console.log(' Resumo dos materiais por tipo:', Object.keys(materialsByType).map(type => ({
      type,
      length: materialsByType[type]?.length || 0
    })));
    return materialsByType;
  } catch (error) {
    console.error(' Erro ao buscar materiais de apoio:', error);
    return {};
  }
};

const generatePrompt = async (type: 'pr' | 'flowchart' | 'tasks', documentContent: string, additionalInfo: string, projectId?: string): Promise<string> => {
  const globalPrompts = await getGlobalPrompts();

  // Usar instrues tcnicas do prompt hardcoded + contexto especfico do global
  const technicalInstructions = DEFAULT_PROMPTS[type];
  const domainContext = globalPrompts[type];

  // Combinar: instrues tcnicas + contexto especfico + contedo
  let finalPrompt = technicalInstructions;

  // Adicionar contexto especfico se existir e for diferente do padro
  if (domainContext && domainContext !== technicalInstructions) {
    finalPrompt += `\n\n--- CONTEXTO ESPECFICO DO DOMNIO ---\n${domainContext}`;
  }

  // Buscar e adicionar materiais de apoio se projectId for fornecido
  if (projectId) {
    console.log(` Buscando materiais de apoio do tipo "${type}" para o projeto: ${projectId}`);
    const supportMaterials = await getProjectSupportMaterials(projectId);
    const projectMaterial = supportMaterials[type];

    if (projectMaterial) {
      finalPrompt += `\n\n--- MATERIAL DE APOIO PERSONALIZADO (${type.toUpperCase()}) ---\n${projectMaterial}`;
      console.log(` Material de apoio ${type} adicionado ao prompt (${projectMaterial.length} caracteres)`);
      console.log(` Contedo do material ${type}:`, projectMaterial.substring(0, 150) + '...');
    } else {
      console.log(` Nenhum material de apoio ${type} encontrado para o projeto ${projectId}`);
      console.log(` Materiais disponveis:`, Object.keys(supportMaterials));
    }
  } else {
    console.log(` Sem projectId fornecido, usando apenas prompts padro`);
  }

  // Adicionar contedo dos documentos
  finalPrompt += `\n\n--- CONTEDO DOS DOCUMENTOS ---\n${documentContent}`;

  // Adicionar informaes adicionais se fornecidas
  if (additionalInfo.trim()) {
    finalPrompt += `\n\n--- INFORMAES ADICIONAIS ---\n${additionalInfo}`;
  }

  return finalPrompt;
}

export const validateApiKey = (): boolean => {
  if (!API_KEY) {
    console.error(' API key do Gemini no configurada');
    console.error(' Configure REACT_APP_GEMINI_API_KEY no arquivo .env');
    console.error(' Obtenha sua API key em: https://makersuite.google.com/app/apikey');
    return false;
  }

  // Verificar se a API key tem formato vlido (chaves do Google AI comeam com "AIza")
  const isValidFormat = API_KEY.startsWith('AIza');
  if (!isValidFormat) {
    console.error(' Formato da API key invlido. As chaves do Google AI comeam com "AIza"');
    console.error(' Sua chave atual:', API_KEY.substring(0, 10) + '...');
    return false;
  }

  console.log(' API key do Gemini configurada corretamente');
  return true;
}

// Funo para testar conexo com Gemini
export const testGeminiConnection = async (): Promise<boolean> => {
  console.log(' Testando conexo com Gemini...');

  if (!validateApiKey()) {
    return false;
  }

  try {
    if (!genAI) {
      console.error(' Falha ao inicializar servio do Gemini');
      return false;
    }

    const model = genAI.getGenerativeModel({ model: 'gemini-2.0-flash-exp' });

    // Teste simples com prompt mnimo
    const testPrompt = 'Responda apenas com "OK" se voc recebeu esta mensagem.';
    const result = await model.generateContent(testPrompt);
    const response = await result.response;
    const text = response.text();

    console.log(' Conexo com Gemini funcionando:', text.trim());
    return true;

  } catch (error: any) {
    console.error(' Erro na conexo com Gemini:', error.message);
    return false;
  }
}

