// Funo para processar documentos e gerar contedo especfico
export const generateContent = async (
  type: 'pr' | 'flowchart' | 'tasks',
  documents: DocumentFile[],
  additionalInfo: string,
  projectId?: string
): Promise<any> => {
  try {
    // Validao da API key
    if (!API_KEY) {
      throw new Error('API key do Gemini no configurada. Configure REACT_APP_GEMINI_API_KEY no arquivo .env');
    }

    if (!genAI) {
      throw new Error('Falha ao inicializar o servio do Gemini. Verifique se a API key  vlida');
    }

    const model = genAI.getGenerativeModel({ model: 'gemini-2.0-flash-exp' });

    // Preparar o contedo dos documentos
    let documentContent = '';
    documents.forEach((doc, index) => {
      documentContent += `\n--- DOCUMENTO ${index + 1}: ${doc.name} ---\n`;
      documentContent += doc.content;
      documentContent += '\n';
    });

    const prompt = await generatePrompt(type, documentContent, additionalInfo, projectId);

    const result = await model.generateContent(prompt);
    const response = await result.response;
    const text = response.text();

    // Processar resposta baseado no tipo
    switch (type) {
      case 'pr':
        // Para PR, retorna o texto diretamente
        return text.trim();

      case 'flowchart':
      case 'tasks':
        // Para flowchart e tasks, tenta extrair JSON
        try {
          let jsonText = '';

          // Mtodo 1: Tentar extrair JSON de blocos de cdigo markdown
          const codeBlockMatch = text.match(/```(?:json)?\s*(\{[\s\S]*?\})\s*```/);
          if (codeBlockMatch) {
            jsonText = codeBlockMatch[1];
          } else {
            // Mtodo 2: Procurar por JSON vlido na resposta
            const jsonMatch = text.match(/\{[\s\S]*\}/);
            if (jsonMatch) {
              jsonText = jsonMatch[0];
            }
          }

          if (jsonText) {
            // Limpar o texto JSON (remover espaos extras, quebras de linha desnecessrias)
            jsonText = jsonText.trim();

            // Verificar se  um JSON vlido antes de parsear
            try {
              JSON.parse(jsonText);
              console.log(` JSON ${type} parseado com sucesso`);
              return JSON.parse(jsonText);
            } catch (parseError: any) {
              console.error(` JSON ${type} invlido:`, parseError);
              console.error('Texto que falhou:', jsonText);
              throw new Error(`JSON ${type} invlido: ${parseError?.message || 'Erro desconhecido'}`);
            }
          } else {
            console.error(' Nenhum JSON encontrado na resposta:', text);
            throw new Error('Resposta da IA no contm JSON vlido');
          }
        } catch (jsonError: any) {
          console.error('Erro ao processar resposta JSON:', jsonError);
          console.error('Resposta completa da IA:', text);
          throw new Error(`Erro na resposta da IA. Formato JSON invlido: ${jsonError?.message || 'Erro desconhecido'}`);
        }

      default:
        throw new Error(`Tipo de contedo no suportado: ${type}`);
    }
  } catch (error: any) {
    console.error(' Erro ao gerar contedo:', error);

    // Tratamento especfico de erros comuns
    if (error.message?.includes('API key')) {
      throw new Error('API key do Gemini no configurada. Configure REACT_APP_GEMINI_API_KEY no arquivo .env');
    }

    if (error.message?.includes('quota') || error.message?.includes('limit')) {
      throw new Error('Limite de uso da API do Gemini excedido. Tente novamente mais tarde');
    }

    if (error.message?.includes('network') || error.message?.includes('fetch')) {
      throw new Error('Erro de conexo. Verifique sua internet e tente novamente');
    }

    // Erro genrico com mais detalhes
    throw new Error(`Falha ao gerar contedo: ${error.message || 'Erro desconhecido'}`);
  }
}

// Funo legada para compatibilidade (gera todos os tipos de contedo)
export const processDocuments = async (documents: DocumentFile[], additionalInfo: string, projectId?: string): Promise<ProjectScope> => {
  console.log(' Iniciando processamento de documentos...');
  console.log(' Documentos recebidos:', documents.length);
  console.log(' Informaes adicionais:', additionalInfo ? 'Sim' : 'No');

  try {
    // Verificar API key primeiro
    if (!API_KEY) {
      throw new Error('API key do Gemini no configurada. Configure REACT_APP_GEMINI_API_KEY no arquivo .env');
    }
    console.log(' API key configurada');

    let prContent = '';
    let flowchartData: any = { nodes: [], edges: [] };
    let tasksData: any = { epics: [], tasks: [] };

    // Gerar PR
    try {
      console.log(' Gerando PR...');
      prContent = await generateContent('pr', documents, additionalInfo, projectId);
      console.log(' PR gerado com sucesso:', prContent.length, 'caracteres');
    } catch (error: any) {
      console.error(' Erro ao gerar PR:', error.message);
      prContent = `Erro ao gerar PR: ${error.message}`;
    }

    // Gerar flowchart
    try {
      console.log(' Gerando fluxograma...');
      flowchartData = await generateContent('flowchart', documents, additionalInfo, projectId);
      console.log(' Fluxograma gerado:', flowchartData.nodes?.length || 0, 'ns,', flowchartData.edges?.length || 0, 'conexes');
    } catch (error: any) {
      console.error(' Erro ao gerar fluxograma:', error.message);
      flowchartData = { nodes: [], edges: [] };
    }

    // Gerar tasks
    try {
      console.log(' Gerando tasks...');
      tasksData = await generateContent('tasks', documents, additionalInfo, projectId);
      console.log(' Tasks geradas:', tasksData.tasks?.length || 0, 'tasks,', tasksData.epics?.length || 0, 'picos');
    } catch (error: any) {
      console.error(' Erro ao gerar tasks:', error.message);
      tasksData = { epics: [], tasks: [] };
    }

    // Converter tasks para o formato esperado
    const tasks: any[] = [];
    if (tasksData.tasks && Array.isArray(tasksData.tasks)) {
      tasksData.tasks.forEach((task: any, index: number) => {
        tasks.push({
          id: `task-${index + 1}`,
          title: task.title,
          description: task.description,
          storyPoints: task.story_points,
          status: 'pending',
          category: task.category,
          criteria: task.acceptance_criteria || []
        });
      });
    }

    const result = {
      title: documents.length > 0 ? documents[0].name : 'Projeto',
      description: prContent.substring(0, 200) + '...',
      flowchart: flowchartData,
      tasks: tasks
    };

    console.log(' Processamento concludo com sucesso!');
    return result;

  } catch (error: any) {
    console.error(' Erro crtico ao processar documentos:', error);

    // Retornar resultado parcial se possvel
    const fallbackResult: ProjectScope = {
      title: documents.length > 0 ? documents[0].name : 'Projeto',
      description: 'Erro ao processar documentos: ' + error.message,
      flowchart: { nodes: [], edges: [] },
      tasks: []
    };

    console.log(' Retornando resultado fallback devido ao erro');
    return fallbackResult;
  }
}

export const extractTextFromFile = async (file: File): Promise<string> => {
  // Implementao bsica para extrao de texto
  // Em um ambiente real, voc precisaria de bibliotecas especficas para cada tipo de arquivo

  if (file.type === 'text/markdown' || file.type === 'text/plain') {
    return await file.text();
  } else if (file.type === 'application/pdf') {
    // Para PDF, voc precisaria de uma biblioteca como pdf-parse
    throw new Error('Processamento de PDF ainda no implementado');
  } else if (file.type.includes('document')) {
    // Para DOCX, voc precisaria de uma biblioteca como mammoth
    throw new Error('Processamento de DOCX ainda no implementado');
  }

  return '';
}
